.cart{
  border:1px solid black;
  width:65%;
  transition:1s;
  height:100%;
  position:fixed;
  margin-left:10rem;
  right:-100%;
  top:0;
  z-index:2;
  font-size:28px;
  text-align:center;
  background-color: white;
  box-shadow: 10px 20px 20px 25px;
  visibility: hidden;
  scroll-behavior: smooth;
}

.cart-header{
  display:flex;
  flex-direction: row;
  justify-content: space-between;
}
.cart-header button{
  width:3rem;
  height:3rem;
  border:transparent;
  font-size:1.5rem;
  background-color: inherit;
  margin-top:1rem;
  margin-right:3rem;
}
.cart-header button:hover{
  cursor:pointer;
}

.close-cart{
  margin-right:5rem;
  margin-top:2rem;
  margin-left:0rem;
}
.close-cart:hover{
  cursor:pointer;
}

.cart-item-div{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom:2rem;
  
}
.cart-item-listing{
  font-size:1.3rem;
  text-emphasis: 100;
  text-align: left;
}

.cart-item-text{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align:center;
}
.cart-pic{
  height:14rem;
  width:14rem;
}

.cart-count{
  display:flex;
  flex-direction: row;
  padding:2px;
  line-height:1.6rem;
}
.cart-count button{
  width:3rem;
  height:2rem;
  margin-right:4rem;
  margin-left: 4rem;
  border:none;
  background-color: hsl(100,30%,50%);
  color:white;
  font-size: 1.3rem;
  border-radius:5px;
}
.cart-count button:hover{
  cursor:pointer;
  background-color: hsl(100,30%,40%);
}
.cart-total{
  font-size:2rem;
  margin-top:4rem;
}
.cart-checkout{
  height:3rem;
  width:17rem;
  font-size: 1.5rem;
  background-color: hsl(100,30%,50%);
  color:white;
  margin-bottom:1rem;
  border:none;
  border-radius:15px;
  font-weight: bolder;
}
.cart-checkout:hover{
  cursor: pointer;
  background-color: hsl(100,30%,40%);
}

@media (min-width:0px) and (max-width:330px){
  .cart-header-text{
   font-size:1.3rem;
   margin-right:1rem;
   margin-bottom:2rem;
  }
  .empty-cart-text{
    font-size:1.2rem;
    margin-top:1rem;
  }
  .close-cart{
    margin-right:1.3rem;
    margin-top:0.9rem;
    margin-left:0rem;
    font-size: 1.3rem;
  }
  .cart-count button{
    width:2.5rem;
    height:1.5rem;
    margin-right:2rem;
    margin-left: 2rem;
    border:none;
    background-color: hsl(100,30%,50%);
    color:white;
    font-size: 1rem;
    border-radius:5px;
  }
  .cart-pic{
    max-height:8rem;
    max-width:8rem;
  }
  .cart-total{
    font-size:1.3rem;
    margin-top:2rem;
  }
  .cart-checkout{
    height:2rem;
    width:10rem;
    font-size: 1rem;
    background-color: hsl(100,30%,50%);
    color:white;
    margin-bottom:1rem;
    border:none;
    border-radius:15px;
    font-weight: bolder;
  }
}
@media (min-width:331px) and (max-width:510px){
  .cart-header-text{
    font-size:1.5rem;
    margin-right:1rem;
    margin-bottom:2.5rem;
   }
   .empty-cart-text{
     font-size:1.2rem;
     margin-top:1rem;
   }
   .close-cart{
     margin-right:1.3rem;
     margin-top:0.9rem;
     margin-left:0rem;
     font-size: 1.3rem;
   }
   .cart-count button{
     width:2.5rem;
     height:1.5rem;
     margin-right:2rem;
     margin-left: 2rem;
     border:none;
     background-color: hsl(100,30%,50%);
     color:white;
     font-size: 1rem;
     border-radius:5px;
   }
   .cart-pic{
     max-height:13rem;
     max-width:13rem;
   }
   .cart-total{
    font-size:1.3rem;
    margin-top:2rem;
  }
  .cart-checkout{
    height:2rem;
    width:10rem;
    font-size: 1rem;
    background-color: hsl(100,30%,50%);
    color:white;
    margin-bottom:1rem;
    border:none;
    border-radius:15px;
    font-weight: bolder;
  }

}

@media (min-width:511px) and (max-width:700px){
  .cart{
    height:200rem;
    top:0;
    z-index: 2;
  }
  .close-cart{
    margin-right:2rem;
    line-height: 3.2rem;
  }
  .visible{
    overflow-y: scroll;
    height:100vh;
    scroll-behavior: smooth;
  }
}

@media(min-width:1500px){
  .cart-header-text{
    font-size:4rem;
    margin-right:0;
    margin-bottom:3.5rem;
   }
   .empty-cart-text{
     font-size:3.5rem;
     margin-top:1rem;
   }
   .close-cart{
     margin-right:8rem;
     margin-top:3.5rem;
     margin-left:0rem;
     font-size: 3rem;
   }
   .cart-count button{
     width:4.5rem;
     height:3.5rem;
     margin-right:5rem;
     margin-left: 5rem;
     border:none;
     background-color: hsl(100,30%,50%);
     color:white;
     font-size: 2.5rem;
     border-radius:5px;
   }
   .cart-item-listing{
     font-size:3rem;
     line-height:2rem;
   }
   .cart-pic{
     min-height:23rem;
     max-height:23rem;
     min-width:23rem;
     max-width:23rem;
   }
   .cart-total{
    font-size:3rem;
    margin-top:4rem;
  }
  .cart-checkout{
    height:5rem;
    width:20rem;
    font-size: 3rem;
    background-color: hsl(100,30%,50%);
    color:white;
    margin-bottom:1rem;
    border:none;
    border-radius:15px;
    font-weight: bolder;
  }
  .pseudo-shop-button{
      min-height:4rem;
      min-width:15rem;
      font-size:2rem;
  }
}
  
